<template>
    <section v-if="isVisible" style="z-index: 17;position: fixed;">
        <transition-group name="fade">
            <LoadingSpinner class="center" v-show="isLoading" key="loadingSpinner"/>


            <div class="image-container"
                 v-show="!isLoading" v-bind:style="{'height': height === -1 ? '70%': height + '%'}"
                 key="img">

                <img ref="img" @click="hide"/>

                <p v-if="text.length !== 0">{{text}}</p>
            </div>

        </transition-group>
    </section>
</template>

<script>
    import {Events} from "../../common/events";
    import imageFetch from "../../common/imageFetch"
    import LoadingSpinner from "./LoadingSpinner";

    export default {
        name: "ImagePreviewContainer",

        components: {LoadingSpinner},

        data: () => {
            return {
                isVisible: false,
                isLoading: false,

                text: "",

                height: -1,
                lastData: null,
            }
        },

        methods: {
            async showImage(data) {
                this.isLoading = true;
                this.isVisible = true;
                this.lastData = data;

                this.$eventBus.$emit(Events.showUnfocusedBackground, {zIndex: 16});
                this.$eventBus.$on(Events.onBackgroundClick, () => {
                    this.hide();
                });
                window.addEventListener('scroll', this.hide);

                let res = await this.downloadImage(data.img);
                this.isLoading = false;

                if (data.height) {
                    this.height = data.height;
                }

                this.$nextTick(() => {
                    this.$refs.img.src = res;
                });

                if (data.text) {
                    this.text = await data.text();
                }
            },

            async downloadImage(src) {
                let rawRes = await imageFetch.download(src);
                return imageFetch.parse(await rawRes.arrayBuffer());
            },

            show() {
                this.isVisible = true;
            },

            hide() {
                if (!this.isVisible) return;

                if (!!this.lastData && !this.lastData.hasOwnProperty("notHideBackgroundOnClick")) {
                    this.$eventBus.$emit(Events.hideUnfocusedBackground);
                }

                this.$eventBus.$off(Events.onBackgroundClick);
                window.removeEventListener('scroll', this.hide);
                this.height = -1;
                this.isVisible = false;
                this.lastData = null;
                this.text = "";
            },
        },

        created() {
            this.$eventBus.$on(Events.showImagePreview, (data) => this.showImage(data));
        },

        destroyed() {
            this.$eventBus.$off(Events.showImagePreview);
        },
    }
</script>

<style lang="sass" scoped>

    .fade-enter-active, .fade-leave-active
        transition: 0.35s

    .fade-enter, .fade-leave-to
        opacity: 0

    .image-container
        position: fixed
        display: flex
        margin: auto
        max-width: 99%
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        box-shadow: 0 14px 28px rgba(0, 0, 0, 0.52), 0 10px 10px rgba(0, 0, 0, 0.5)
        font-family: Roboto, sans-serif

        img
            height: 100%

        p
            width: 200px
            padding: 15px
            margin: 0
            overflow-y: scroll
            word-break: break-word
            font-size: 1rem
            line-height: 1.3
            color: black
            background-color: #FFFFFF

    .center
        margin: auto
        position: fixed
        top: 0
        left: 0
        bottom: 0
        right: 0
</style>